						</div>
					</b-col>
					<b-col cols="4" class="d-none d-lg-block">
						<div class="d-flex position-absolute fs-sm text-muted" style="top: 17px" data-visible="showIntro">
							<BIcon icon="info-circle" scale="1.5" class="mt-1" />
							<p class="ml-3 mb-0">{{ $t("showIntroDesc") }}</p>
						</div>
					</b-col>

					<!-- Invite show on your profile page -->
					<b-col cols="12" lg="8" class="mb-5 mt-3">
						<div v-visible="'showInYourProfilePage'">
