								<div>
									<BIcon icon="person-circle" scale="1.5" class="mt-1" style="color: #5e5e5e" />
									<span class="text-muted mx-3 pl-1">{{ $t("showInYourProfilePage") }}</span>
								</div>
								<div class="text-primary cursor-pointer font-weight-500">
									<b-checkbox v-model="invite.showInYourProfilePage" switch size="lg" />
								</div>
							</div>
						</div>
					</b-col>
					<b-col cols="4" class="d-none d-lg-block">
						<div class="d-flex position-absolute fs-sm text-muted" style="top: 17px" data-visible="showInYourProfilePage">
							<BIcon icon="info-circle" scale="1.5" class="mt-1" />
							<p class="ml-3 mb-0">{{ $t("showInYourProfilePageDesc") }}</p>
						</div>
					</b-col>
				</b-row>

				<div class="field_title pt-3 mb-4">
					<div class="title">{{ $t("schedulingOptions") }}</div>
				</div>

				<b-row>
					<!-- Invite minimum notice -->
					<b-col cols="12" lg="8" class="mb-5">
						<form-select :label="$t('minimumNotice')" v-model="invite.minimumNotice" :options="minimumNoticeOptions" v-visible="'minimumNotice'">
							<BIcon slot="icon_left" scale="1.1" icon="alarm" style="color: #5e5e5e" />
						</form-select>
					</b-col>
					<b-col cols="4" class="d-none d-lg-block">
