							</p>
						</div>
					</b-col>

					<!-- Invite buffer event -->
					<b-col cols="12" lg="8" class="mb-5">
						<div class="d-flex" v-visible="'bufferEvent'">
							<form-select :label="$t('bufferBeforeEvent')" v-model="invite.bufferBeforeEvent" :options="bufferEventOptions" class="pr-3">
								<BIcon slot="icon_left" icon="arrow-left-right" style="color: #5e5e5e" />
							</form-select>
							<form-select :label="$t('bufferAfterEvent')" v-model="invite.bufferAfterEvent" :options="bufferEventOptions" class="pl-3"> </form-select>
