					</b-col>
					<b-col cols="4" class="d-none d-lg-block">
						<div class="d-flex position-absolute fs-sm text-muted" style="top: 25px" data-visible="bufferEvent">
							<BIcon icon="info-circle" scale="1.5" class="mt-1" />
							<p class="ml-3 mb-0">
								{{ $t("bufferEventDesc") }}
							</p>
						</div>
					</b-col>

					<!-- Invite limit max number of events -->
					<b-col cols="12" lg="8" class="mb-5 mt-3">
						<div v-visible="'limitMaxNumberEvents'">
							<div class="d-flex align-items-center justify-content-between">
								<div>
									<div class="d-flex">
										<BIcon icon="calendar4-week" scale="1.4" class="mt-1" style="color: #5e5e5e" />
										<div class="mx-3 pl-1">
											<div class="text-muted mt-1">{{ $t("limitMaxNumberEvents") }}</div>
											<div class="mt-4 d-flex align-items-center" v-if="invite.limitMaxNumberEvents">
												<b-form-input v-model="invite.maxNumberEvents" type="number" class="pl-4 pr-2" style="width: 70px; height: 51px" />
												<span class="mx-3">{{ $t("per") }}</span>
												<b-form-select
													v-model="invite.maxNumberEventsPer"
													:options="maxNumberEventsPerOptions"
													class="pl-3 fs-md"
													style="width: 100px; height: 51px"
												/>
											</div>
