									</div>
								</div>
								<div class="text-primary cursor-pointer font-weight-500 align-self-start">
									<b-checkbox v-model="invite.limitMaxNumberEvents" switch size="lg" />
								</div>
							</div>
						</div>
					</b-col>
					<b-col cols="4" class="d-none d-lg-block">
						<div class="d-flex position-absolute fs-sm text-muted" style="top: 17px" data-visible="limitMaxNumberEvents">
							<BIcon icon="info-circle" scale="1.5" class="mt-1" />
