					<b-button class="filter_btn active mr-3 font-weight-500" size="sm" @click="back">
						<BIcon :icon="`${!$isRtl ? 'chevron-left' : 'chevron-right'}`" scale="0.8" class="mr-2" />
						{{ $t("back") }}
					</b-button>
					<b-button variant="primary" @click="createInvite" class="px-4"> {{ $t("createInvite") }} </b-button>
				</div>
			</b-container>
		</div>
		<PreviewInvite ref="preview" />
	</div>
</template>

<script>
	import FormInput from "@/components/inputs/FormInput";
	import FormSelect from "@/components/inputs/FormSelect";
	import VirtualInput from "@/components/inputs/VirtualInput";
	import EarthIcon from "@/components/icons/earth";
	import PersonIcon from "@/components/icons/person";
	import PreviewInvite from "@/components/PreviewInvite";

	import store from "@/store";

	import { validationMixin } from "vuelidate";

	import { required, minValue, maxValue, numeric } from "vuelidate/lib/validators";

	export default {
		components: {
			FormInput,
			VirtualInput,
			FormSelect,
			EarthIcon,
			PersonIcon,
			PreviewInvite,
		},

		mixins: [validationMixin],

		data() {
			return {
				invite: {
					name: "",
					eventNameFormat: "invitee_organizer",
					displayedTimezone: "local",
					showIntro: true,
					showInYourProfilePage: true,
					minimumNotice: 360,
					bufferBeforeEvent: 0,
					bufferAfterEvent: 0,
					limitMaxNumberEvents: false,
					maxNumberEvents: 3,
					maxNumberEventsPer: "day",
				},
				displayedTimezoneOptions: [
					{ value: "local", text: this.$t("inviteeLocalTimezone") },
					{ value: "locked", text: this.$t("lockedTimezone") },
				],
				minimumNoticeOptions: [
					{ value: 15, text: this.$t("15min") },
					{ value: 60, text: this.$t("1hour") },
					{ value: 360, text: this.$t("6hours") },
					{ value: 720, text: this.$t("12hours") },
					{ value: 1440, text: this.$t("1day") },
					{ value: 2880, text: this.$t("2days") },
					{ value: 10080, text: this.$t("7days") },
				],
				bufferEventOptions: [
					{ value: 0, text: this.$t("0min") },
					{ value: 5, text: this.$t("5min") },
					{ value: 10, text: this.$t("10min") },
					{ value: 15, text: this.$t("15min") },
					{ value: 30, text: this.$t("30min") },
					{ value: 60, text: this.$t("1hour") },
					{ value: 120, text: this.$t("2hours") },
					{ value: 240, text: this.$t("4hours") },
				],
				maxNumberEventsPerOptions: [
					{ value: "day", text: this.$t("day") },
					{ value: "week", text: this.$t("week") },
					{ value: "month", text: this.$t("month") },
				],
			};
		},

		validations: {
			invite: {
				eventNameFormat: {},
				showIntro: {},
				showInYourProfilePage: {},
				minimumNotice: {},
				bufferBeforeEvent: {},
				bufferAfterEvent: {},
				limitMaxNumberEvents: {},
				maxNumberEvents: { requiredIf: (model) => model.limitMaxNumberEvents, numeric, minValue: minValue(1), maxValue: maxValue(100) },
				maxNumberEventsPer: {},
				allowGuests: {},
			},
		},

		async beforeRouteEnter(to, from, next) {
			let invite = store.state.invites.one;

			if (!invite.name) return next("/create/group");

			next((vm) => vm.init(invite));
		},

		mounted() {},

		computed: {
			eventNameFormatOptions() {
				let invitee = this.$t("invitee");

				let organizer = this.$me.username;

				let eventName = this.invite.name;

				let and = this.$t("and");

				return [
					{
						value: "invitee_organizer",
						text: `[${invitee}] ${and} ${organizer}`,
					},
					{
						value: "invitee_organizer_name",
						text: `[${invitee}] ${and} ${organizer}: ${eventName}`,
					},
					{
						value: "name_invitee_organizer",
						text: `${eventName}: [${invitee}] ${and} ${organizer}`,
					},
				];
			},
		},

		methods: {
			init(invite) {
				for (let key in invite) {
					this.invite[key] = invite[key];
				}
			},

			preview() {
				this.$store.commit("setInvite", this.invite);

				this.$nextTick(() => this.$refs.preview.open());
			},

			createInvite() {
				this.$v.$touch();

				if (this.$v.$invalid) return;

				this.$store.commit("setInvite", this.invite);

				// this.$router.push("/create/group/done");
			},

			back() {
				this.$store.commit("setInvite", this.invite);

				this.$router.push("/create/group/when");
			},
		},
	};
</script>

<style lang="scss"></style>
